import React,{Component} from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';

import Sct1_1_bg from './Sct1_1_bg';
import Sct1_1_date from './Sct1_1_date';
import Sct1_1_p from './Sct1_1_p';

class Sct1_1 extends Component{
    constructor(props){
        super(props);
    }
    render(){
        let link = `/ReactBlog/menu/${String(this.props.link)}`;
        return (
            <div className='sct1_1' >
                <Sct1_1_bg  source={this.props.source} />
                <Link to={link} ><Sct1_1_p text={this.props.text} /></Link>
                <Sct1_1_date date={this.props.date} name={this.props.name}/>
            </div>
        );
    }
}

Sct1_1.propTypes = {
    source: PropTypes.string,
    text: PropTypes.string,
    date: PropTypes.string,
    name: PropTypes.string,
    link: PropTypes.number,
};

export default Sct1_1;