import React,{Component} from 'react';
import {Switch, Route} from 'react-router-dom';
import Content from './api.js';
import Sct2_big from './Sct2_big';
import Sct2_menu_t from './Sct2_menu_t';
import {Link} from 'react-router-dom';

class Sct2_menu extends Component{
    render(){
        return(
            <div id='sct2_menu' >
                <Switch>
                    <Route exact path='ReactBlog/menu' component={Sct2_big} />
                    {Content.all().map((item,index) => {
                        return (
                            <Route key={index} path={`ReactBlog/menu/${item.link}`} render={(props) => (<Sct2_big {...props} source={item.img} name={item.name} text={item.text} date={item.date} />) } />
                        );
                    })}
                </Switch>
                <Sct2_menu_t />
                <Link to='/ReactBlog/' >&#8592; Back</Link>
            </div>
        );
    }
}
export default Sct2_menu;