import React, { Component } from 'react';
import { Carousel, CarouselCaption, CarouselInner, CarouselItem, View, Mask } from 'mdbreact';

// images
import img1 from '../img/Boxes/1.jpg';
import img2 from '../img/bottle.jpg';
import img3 from '../img/food.jpg';
import img4 from '../img/orbit.jpg';

class Slider extends Component {
    render() {
        return (
            <div>
                <Carousel activeItem={1} length={4} showControls={true} showIndicators={false} className="z-depth-1">
                    <CarouselInner>
                        <CarouselItem itemId="1">
                            <View>
                                <img className="d-block w-100" src={img1} alt="First slide" />
                                <Mask overlay="black-light" />
                                <div className='headergirl' ></div>
                            </View>
                            <CarouselCaption>
                                <h3 className="h3-responsive">Maecans quis lobortis nunc. Nullam sit vel odio congue vulputate a ut nisi.</h3>
                            </CarouselCaption>
                        </CarouselItem>
                        <CarouselItem itemId="2">
                            <View>
                                <img className="d-block w-100" src={img2} alt="Second slide" />
                                <Mask overlay="black-strong" />
                            </View>
                            <CarouselCaption>
                                <h3 className="h3-responsive">Donec elementum dui semper, pretium dui quis, pretium nisl. Nunc quis ornare odio.</h3>
                            </CarouselCaption>
                        </CarouselItem>
                        <CarouselItem itemId="3">
                            <View>
                                <img className="d-block w-100" src={img3} alt="Third slide" />
                                <Mask overlay="black-slight" />
                            </View>
                            <CarouselCaption>
                                <h3 className="h3-responsive">Sed arcu ipsum, convallis quis porttitor bibendum, convallis non enim.</h3>
                            </CarouselCaption>
                        </CarouselItem>
                        <CarouselItem itemId="4">
                            <View>
                                <img className="d-block w-100" src={img4} alt="Fourth slide" />
                                <Mask overlay="black-light" />
                            </View>
                            <CarouselCaption>
                                <h3 className="h3-responsive">Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae.</h3>
                            </CarouselCaption>
                        </CarouselItem>
                    </CarouselInner>
                </Carousel>
            </div>
        );
    }
}

export default Slider;