import React,{Component} from 'react';
import Sct1_1 from './Sct1_1';
import Content from './api';

class Sct1 extends Component{
    render(){
        return (
            <div className='sct1parent'>
                <div id='sct1'>
                    {Content.all().map((item, index) => {
                        if (index < 3 && index > 0) {
                            return (
                                <Sct1_1
                                    key={index}
                                    source={item.img}
                                    text={item.text}
                                    date={item.date}
                                    name={item.name}
                                    link={item.link}  />
                            );}})}
                </div>
            </div>
        );
    }
}


export default Sct1;