import React, {Component} from 'react';

class Search extends Component{
    constructor(props){
        super(props);
        this.state = {
            bool: true,
            inp: ''
        };
        this.inputHide = this.inputHide.bind(this);
        this.change = this.change.bind(this);
        this.sub = this.sub.bind(this);

    }
    
    inputHide(){
        this.setState({
            bool: !this.state.bool
        });
    }
    
    sub(e){
        e.preventDefault();
        this.setState({ inp: '' });
    }

    change(e){
        this.setState({ inp: e.target.value });
    }

    render(){
        let clsn = 'search';
        this.state.bool ? clsn = clsn+= ' inpShow' : clsn += ' inpHide';
        return(
            <form onSubmit={this.sub}>
                <div className={clsn} >
                    <label onClick={this.inputHide} htmlFor="#inp"></label>
                    <input value={this.state.inp} onChange={this.change} placeholder='Search something' type="text"/>
                    <ul id="srch-ul">
                        <li><p>Maecenas quis lobortis nunc. Nullam sit amet augue vel odio congue vulputate a ut nisi.</p></li>
                        <li><p>Donec elementum dui semper, pretium dui quis, pretium nisl. Nunc quis ornare odio.</p></li>
                        <li><p>Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae.</p></li>
                        <li><p></p>Sed arcu ipsum, convallis quis porttitor bibendum, convallis non enim.</li>
                    </ul>
                </div>
            </form>
        );
    }
}

export default Search;