import React, { Component } from 'react';
import Sct1_1 from './Sct1_1';
import Content from './api';

class Sct2_b extends Component{
    render(){
        return(
            <div className='sct2_b' >
                {Content.all().map((item, index) =>{
                    if (index==3) {
                        return(
                            <Sct1_1 key={index} source={item.img} text={item.text} date={item.date} name={item.name} link={item.link} />
                        );}})}
            </div>
        );
    }
}
export default Sct2_b;