import React,{Component} from 'react';
import {Link} from 'react-router-dom';

class Blogtitle extends Component{
    render(){
        return(
            <Link to='/' >
                <div className='title' >
                    <p>Blog <span>&#160;template</span></p>
                </div>
            </Link>
        );
    }
}
export default Blogtitle;