import  React,{Component} from 'react';

class Footer extends Component{
    constructor(props){
        super(props);
        this.state = {
            input: ''
        };
        this.submit = this.submit.bind(this);
        this.change = this.change.bind(this);
    }
    
    submit(e){
        e.preventDefault();
        this.setState({ input: ''});
    }
    
    change(e){
        this.setState({input: e.target.value});
    }
    render(){
        return(
            <div className='ff' >
                <div className='footer' >
                    <div> 
                        <h2>Newsletter</h2>
                        <p>Donee elementum dui semper, pretium dui quis, pretium nisl.</p>
                        <form onSubmit={this.submit} className='ftform' action="">
                            <input onChange={this.change} value={this.state.input} type="text"/>
                            <button type='submit' >Sign up</button>
                        </form>
                    </div>
                </div>
                <div className='footerend' >
                    <p>Cum sociis natoque penatibus el magnis dis parturient montes, nascetur ridiculus mus. <br/><br/> Try awesome tool for designers <strong>symo.co</strong></p>
                </div>
            </div>
        );
    }
}

export default Footer;