import React,{Component} from 'react';
import Sct1_1_bg from './Sct1_1_bg';
import Sct1_1_date from './Sct1_1_date';
import Sct1_1_p from './Sct1_1_p';
import PropTypes from 'prop-types';

class Sct2_big extends Component{
    render(){
        return(
            <div className='sct1_1' >
                <Sct1_1_bg bool={true}  source={this.props.source} />
                <Sct1_1_p text={this.props.text} />
                <Sct1_1_date date={this.props.date} name={this.props.name}/>
            </div>
        );
    }
}

Sct2_big.propTypes = {
    text: PropTypes.string,
    date: PropTypes.string,
    name: PropTypes.string,
    source: PropTypes.string
};

export default Sct2_big;