import React,{Component} from 'react';
import PropTypes from 'prop-types';

class Sct1_1_p extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div>
                <p>{this.props.text}</p>
            </div>
        );
    }
}

Sct1_1_p.propTypes = {
    text: PropTypes.string
};

export default Sct1_1_p;