import React,{Component} from 'react';
import Sct2_page from './Sct2_page';
import Sct2_menu from './Sct2_menu';
import {Switch, Route} from 'react-router-dom';
// import { CSSTransition, TransitionGroup} from 'react-transition-group';


class Sct2 extends Component{
    constructor(props){
        super(props);
    }

    render(){
        return (
            <div id='sct2'>
                <Switch  >
                    <Route exact path='/ReactBlog' component={Sct2_page} />
                    <Route path='/ReactBlog/menu' component={Sct2_menu} />
                </Switch>
            </div>
        );
    }
}


export default Sct2;


