import React,{Component} from 'react';
import PropTypes from 'prop-types';

class Sct1_1_bg extends Component{
    constructor(props){
        super(props);
    }
    render(){
        var img;
        if (this.props.bool) {
            img = `url('./${this.props.source}')`;
        } else{
            img = `url('../${this.props.source}')`;
        }
        return (
            <div className='bg' style={{
                backgroundImage: img,
            }}>
                <p></p>
            </div>
        );
    }
}


Sct1_1_bg.propTypes = {
    source: PropTypes.string,
    bool: PropTypes.bool
};

export default Sct1_1_bg;