import React,{Component} from 'react';
import Search from './Search';
import Big_ul from './Big_ul';
import Small_ul from './Small_ul';
import Blogtitle from './Blogtitle';


class Nav extends Component {
    render(){
        return(
            <nav>
                <Blogtitle/>
                <Search/>
                <Small_ul/>
                <Big_ul/>
            </nav>
        );
    }
}

export default Nav;