import React,{Component} from 'react';
import PropTypes from 'prop-types';

class Sct1_1_date extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div>
                <p>{this.props.date}</p>
                <a href='#'> {this.props.name}</a>
            </div>
        );
    }
}

Sct1_1_date.propTypes = {
    date: PropTypes.string,
    name: PropTypes.string,
};

export default Sct1_1_date;