import React,{Component} from 'react';
import Sct1_1 from './Sct1_1';
import Sct2_b from './Sct2_b';
import Content from './api';


class Sct2_page extends Component{
    render(){
        return(
            <div id='sct2_page'>
                <Sct2_b />
                {Content.all().map((item, index) => {
                    if (index > 3 && index < 10) {
                        return (
                            <Sct1_1
                                key={index}
                                source={item.img}
                                text={item.text}
                                date={item.date}
                                name={item.name}
                                link={item.link} />
                        );}})} 
            </div>
        );
    }
}
export default Sct2_page;